import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import Image from "gatsby-image"
import styles from "../components/courses.module.css"
import { Link } from "gatsby"

const CoursesPage = ({ data }) => {
  const {
    allContentfulCourse: { nodes: courses },
  } = data
  return (
    <Layout>
      <div className={styles.coursesContainer}>
        {courses.map(course => {
          return (
            <div className="course-container" key={course.slug}>
              <div className={styles.courseDetails}>
                <div className="course-image">
                  <Image fluid={course.image.fluid} />
                </div>
                <div className="course-title">
                  <h1>{course.title}</h1>
                </div>
                <div className="course-info">
                  <ul className={styles.courseInfo}>
                    {course.info.map(info => (
                      <li key={info}>{info}</li>
                    ))}
                  </ul>
                </div>
                <Link to={`/products/${course.slug}`}>تفاصيل أكثر ...</Link>
              </div>
            </div>
          )
        })}
      </div>
    </Layout>
  )
}

export const query = graphql`
  {
    allContentfulCourse {
      nodes {
        slug
        image {
          fluid {
            ...GatsbyContentfulFluid
          }
        }
        title
        description {
          description
        }
        info
      }
    }
  }
`

export default CoursesPage
